.button{
    background-color: #3D3C40;
    width: 90%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #F2B705;
    font-weight: 600;
    cursor: pointer;
    border-style: groove;
    border-color: #F2B705;
    transition: all 0.2s ease-out;
     
    &:hover{
        background-color: #78768085;

    }
    &:active{
        border-style: none;
    }
}